  title: '',
  format: [],
  amount: 1,
}
export default {
  components: {
    ResourceView,
  },
  data() {
    const mockData = []
    for (let i = 0; i < 20; i++) {
      mockData.push({
        key: i.toString(),
        title: `选手${i + 1}`,
        description: `选手描述${i + 1}`,
      })
    }
    const oriTargetKeys = mockData.filter((item) => +item.key % 3 > 1).map((item) => item.key)
    return {
      currentStep: 0,
      resType: 1,
      mockData,
      targetKeys: oriTargetKeys,
      selectedKeys: ['1', '4'],
      labelCol: { span: 2 },
      wrapperCol: { span: 10 },
      form: {
        proj: '',
        name: '',
        startDate: '',
        endDate: '',
        taskType: undefined,
        needRate: true,
        needLog: true,
        address: '',
        logInterval: undefined,
      },
      showUploadModal: false,
      uploadList: [],
      uploadForm,
      fileList: [],
      saving: false,
      nodeList: [
        { key: '1', title: '简介' },
        { key: '2', title: '任务描述' },
        { key: '3', title: '编程环境' },
        { key: '4', title: '接口表', children: [] },
        {
          key: '5',
          title: '游戏说明',
          children: [
            { key: '5-1', title: '定义说明' },
            { key: '5-2', title: '操作说明' },
            { key: '5-3', title: '游戏裁决', isLeaf: true },
          ],
        },
        {
          key: '6',
          title: '编程任务',
          children: [
            { key: '6-1', title: '第一阶段编程任务' },
            { key: '6-2', title: '第二阶段编程任务' },
            {
              key: '6-3',
              title: '第三阶段编程任务',
              children: [
                { key: '6-3-1', title: '任务 3.1：游戏计时', children: [] },
                { key: '6-3-2', title: '任务 3.2：光标功能', children: [] },
                { key: '6-3-3', title: '任务 3.3：调试功能', children: [] },
              ],
            },
          ],
        },
      ],
      resourceList: [
        {
          id: 1,
          title: '资源名称资源名称称01',
        },
        {
          id: 2,
          title: '资源名称资源名称资源名称02',
        },
        {
          id: 3,
          title: '资源名称资源名称资源03',
        },
        {
          id: 4,
          title: '资源名称资源源名称资源04',
        },
        {
          id: 5,
          title: '资源名称资源名称资05',
        },
        {
          id: 6,
          title: '资源名称资源名称资源称资源名66666称06',
        },
        {
          id: 7,
          title: '资源名称资源名称资源名称资源07',
        },
        {
          id: 8,
          title: '资源名称资源名称资源名称资源名08',
        },
      ],
      expandedKeys: ['1', '2', '3', '4', '5', '6', '6-3'],
    }
  },
  methods: {
    load() {
      let list = []
      for (let index = 1; index < 20; index++) {
        list.push({
          id: index,
          title: 'B模块 嵌入式编程' + index,
          people: 1345,
        })
      }
      this.list = list
    },
    onTabChange(key) {
      document.querySelector('#' + key).scrollIntoView(true)
    },
    onDateRangeChange(dates, dateStrings) {
      let [startDate, endDate] = dateStrings
      this.form.startDate = startDate
      this.form.endDate = endDate
    },
    handleUploadChange(info) {
      const status = info.file.status
      if (status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (status === 'done') {
        this.$message.success(`${info.file.name} file uploaded successfully.`)
      } else if (status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`)
      }
      this.fileList = info.fileList
    },
    handlePreview() {},
    onSubmit() {
      this.saving = true
      setTimeout(() => {
        this.currentStep = 2
        this.saving = false
      }, 600)
    },
    handleAssignChange(nextTargetKeys, direction, moveKeys) {
      this.targetKeys = nextTargetKeys
      console.log('targetKeys: ', nextTargetKeys)
      console.log('direction: ', direction)
      console.log('moveKeys: ', moveKeys)
    },
    handleAssignSelectChange(sourceSelectedKeys, targetSelectedKeys) {
      this.selectedKeys = [...sourceSelectedKeys, ...targetSelectedKeys]

      console.log('sourceSelectedKeys: ', sourceSelectedKeys)
      console.log('targetSelectedKeys: ', targetSelectedKeys)
    },

    onNodeSelect(keys, data) {
      // console.log('onNodeSelect', keys, data.node.eventKey)
      const node = data.node
      console.log(document.querySelector(`#tab${node.eventKey}`))
      scrollToElement(document.querySelector(`#tab${node.eventKey}`))
    },
    onAddUploadRow() {
      this.uploadForm = { ...uploadForm }
      this.showUploadModal = true
    },
    onUploadSubmit() {
      this.uploadList.push(this.uploadForm)
      // this.uploadForm = { ...uploadForm }
      this.showUploadModal = false
    },
    onEditUpload(item) {
      this.uploadForm = { ...item }
      this.showUploadModal = true
    },
    onDelUpload(idx) {
      this.uploadList.splice(idx, 1)
      // this.$forceUpdate()
    },
  },
  mounted() {
    this.load()
  },
}
</script>

